.footer {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 79px 70px 20px 70px;
}

.footer__title {
    margin: 0;
    padding: 0 0 20px;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    color: #a0a0a0;
    border-bottom: 1px solid #e8e8e8;
}

.footer__container {
    display: flex;
    margin-top: 20px;
}

.footer__author {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    flex: 1 1 auto;
}

.footer__link {
    padding: 0;
    margin: 0 0 0 20px;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    color: #000;
    text-decoration: none;
}

.footer__link:hover {
    color: #a0a0a0;
}

@media screen and (max-width: 768px) {
    .footer {
        padding: 79px 30px 20px 30px;
    }
}

@media screen and (max-width: 320px) {
    .footer {
        padding: 79px 10px 10px 10px;
    }

    .footer__title {
        padding-bottom: 21px;
        font-size: 12px;
        line-height: 15px;
    }

    .footer__container {
        margin-top: 30px;
        flex-direction: column;
        align-items: center;
    }

    .footer__author {
        font-size: 12px;
        line-height: 15px;
        color: #a0a0a0;
        order: 10;
        margin-top: 18px;
    }

    .footer__link {
        margin-left: 0;
        font-size: 12px;
        line-height: 15px;
        margin-bottom: 12px;
    }
}
