.not-found{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.not-found__title{
    margin-top: 246px;
    font-style: normal;
    font-weight: 400;
    font-size: 140px;
    line-height: 169px;
    text-align: center;
}

.not-found__text{
    margin-top: 5px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
}

.not-found__back-button{
    margin-top: 184px;
    margin-bottom: 60px;
    min-height: 17px;
    text-decoration: none;
    color: black;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
}

.not-found__back-button:hover{
    opacity: 0.6;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .not-found__title{
        margin-top: 408px;
    }

    .not-found__back-button{
        margin-bottom: 222px;
    }
}

@media screen and (max-width: 480px) {
    .not-found__title{
        margin-top: 329px;
        font-size: 80px;
        line-height: 97px;
    }

    .not-found__text{
        margin-top: 10px;
        font-size: 12px;
        line-height: 15px;
    }

    .not-found__back-button{
        margin-top: 284px;
        margin-bottom: 30px;
        min-height: 15px;
        font-size: 12px;
        line-height: 15px;
    }
}