.techs {
    box-sizing: border-box;
    padding: 100px 70px;
    background-color: #f5f5f5;
}

.techs__container {
    min-height: 415px;
    display: flex;
    flex-direction: column;
}

.techs__title {
    margin: 0;
    padding: 0 0 23px;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: -0.04em;
    border-bottom: 1px solid #000000;
}

.techs__main-title {
    padding: 0;
    margin: 90px auto 26px;
    font-style: normal;
    font-weight: 400;
    font-size: 50px;
    line-height: 58px;
    text-align: center;
    letter-spacing: -0.04em;
    max-width: 730px;
}

.techs__description {
    margin: 0 auto;
    padding: 0 0 100px;
    max-width: 460px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
}

.techs__list {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, 90px);
    gap: 10px;
    justify-content: center;
    list-style: none;
}

.techs__list-item {
    margin: 0;
    padding: 0;
    width: 90px;
    height: 60px;
    border-radius: 10px;
    background-color: #e8e8e8;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

@media screen and (max-width: 768px) {
    .techs {
        padding: 90px 50px;
    }

    .techs__container {
        min-height: 387px;
    }

    .techs__main-title {
        margin: 80px auto 22px;
        max-width: 600px;
    }

    .techs__description {
        padding-bottom: 83px;
        font-size: 12px;
        line-height: 18px;
    }

    .techs__list {
        grid-template-columns: repeat(auto-fit, 84px);
    }

    .techs__list-item {
        margin: 0 auto;
        width: 84px;
        height: 57px;
        font-size: 12px;
        line-height: 15px;
    }
}

@media screen and (max-width: 320px) {
    .techs {
        padding: 70px 18px;
        background-color: #ffffff;
    }

    .techs__title {
        padding-bottom: 28px;
        font-size: 18px;
        line-height: 22px;
    }

    .techs__main-title {
        padding: 0;
        margin: 60px auto 24px;
        font-size: 30px;
        line-height: 36px;
        max-width: 292px;
    }

    .techs__description {
        margin: 0 auto;
        padding: 0 0 50px;
        max-width: 292px;
        font-size: 11px;
        line-height: 16px;
    }

    .techs__container {
        min-height: 511px;
    }

    .techs__list {
        grid-template-columns: repeat(2, 84px);
    }
}
